.main .portfolio {
  .link {
    font-size: 0.75rem;
  }

  .grid {
    display: grid;
    grid-template-areas:
      'preview description'
      'links   description';
    grid-template-columns:
      [preview] 50fr
      [description] 50fr;
    grid-template-rows: [preview] 6rem;
  }

  .card {
    border: 1px solid $primary;
    border-radius: 5px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  }
  .card-img-container {
    display: block;
    border-bottom: 1px solid $primary;
  }
  .card-img {
    display: block;
    width: 100%;
    border-radius: 5px;
  }
  .card-content {
    padding: 1rem;
    border-bottom: 1px solid $secondary;

    .card-title {
      font-size: 2rem;
    }
  }
  .card-info {
    display: flex;
    justify-content: center;
    padding: 0.25rem 1rem;
    color: $primary;
    background-color: rgba($secondary, 0.4);
    font-family: Fira Code;
    border-radius: 0 0 5px 5px;

    .feather {
      height: 1rem;
      margin-right: 0.5rem;
    }

    .links .link:not(:first-child) {
      margin-left: 1rem;
    }
  }

  // Override
  @media (min-width: $lg) {
    .preview-date {
      position: absolute;
      top: 0;
      left: 0;
      bottom: auto;
    }
  }
}
